import React, {Component} from 'react';
import {Grid, Row, Col, Image, Glyphicon} from 'react-bootstrap';


const getFeaturedImage = (id) => {
  if (id === 0) {
    return Promise.resolve(undefined)
  }

  return fetch(`http://tks.rxn.mybluehost.me/wp-json/wp/v2/media/${id}`)
    .then(res => res.json())
    .then(json => json.guid.rendered)
}

const convertPostToSchedule = (post) => {
  return {
    id: post.id,
    name: post.title.rendered,
    teaser: post.excerpt.rendered,
    date: post.acf.start_date,
    location: post.acf.location,
    featuredImage: getFeaturedImage(post.featured_media),
    attendees: post.acf.attendees,
    featured: post.acf.featured
  };
};

class ScheduleTeaser extends Component {
  constructor(props) {
    super(props)
    this.state = {featuredImageSrc: undefined}
  }

  render() {
    const {schedule} = this.props
    return (
      <Row className="clearfix schedule-item">
        <Col sm={3} md={3}>
          {this.state.featuredImageSrc &&
            <a href={`/#/scheduled_courses/${schedule.id}`}><Image responsive alt={schedule.name} src={this.state.featuredImageSrc} className="featured-image"/></a>
          }
        </Col>
        <Col md={6}>
          <h3>
            <a href={`/#/scheduled_courses/${schedule.id}`} dangerouslySetInnerHTML={{__html: schedule.name, sanitize: true}}></a>
            {schedule.featured &&
              <div className="featured"></div>
            }
          </h3>
          <p><Glyphicon glyph="map-marker"/>{schedule.location}</p>
          <div dangerouslySetInnerHTML={{__html: schedule.teaser}} />
        </Col>
        <Col md={3}>
          <h4 className="date"><Glyphicon glyph="calendar"/>{schedule.date}</h4>
          <h4><Glyphicon glyph="thumbs-up" className="highlight-text" />Đối tượng tham gia</h4>
          <p>{schedule.attendees}</p>
        </Col>
      </Row>
    )
  }

  componentDidMount() {
    const {schedule} = this.props
    schedule.featuredImage.then(featuredImageSrc => this.setState({featuredImageSrc}))
  }
}

class ScheduleTeasers extends Component {
  constructor(props) {
    super(props);
    this.state = {schedules: undefined};
  }

  render() {
    if (!this.state.schedules) {
      return <div className="loader-wrapper"><div className="loader"><Glyphicon glyph="plane"/></div></div>;
    }

    return (      
      <div>
        <Grid className="schedules space-bottom">
          {this.state.schedules.map(s => <ScheduleTeaser key={s.id} schedule={s} />)}
        </Grid>
      </div>
    );
  }

  componentDidMount() {
    this._loadSchedules();
  }

  _loadSchedules() {
    // https://developer.wordpress.com/docs/api/
    // fetch('https://public-api.wordpress.com/rest/v1.1/sites/pdi2017.wordpress.com/posts/?category=schedules')
    fetch('http://tks.rxn.mybluehost.me/wp-json/wp/v2/courses')
      .then(res => res.json())
      .then(json => json.filter(post => post.acf.scheduled).map(convertPostToSchedule))
      .then(schedules => this.setState({schedules}));
  }
};

export default ScheduleTeasers;



