import React, {Component} from 'react';
import {Route} from 'react-router-dom';
import {Grid, Row, Col, Glyphicon, Image, Button} from 'react-bootstrap';


const getFeaturedImage = (id) => {
  return fetch(`http://tks.rxn.mybluehost.me/wp-json/wp/v2/media/${id}`)
    .then(res => res.json())
    .then(json => json.guid.rendered)
}

const convertPostToEvent = (post) => {
  return {
    id: post.id,
    name: post.title.rendered,
    teaser: post.excerpt.rendered,
    date: post.acf.date? post.acf.date : new Date(post.date).toLocaleDateString(),
    start: post.acf.start_time,
    end: post.acf.end_time,
    location: post.acf.location,
    featuredImage: getFeaturedImage(post.featured_media),
    content: post.content.rendered,
    featured: post.acf.featured
  };
};

class FeaturedImage extends Component {
  constructor(props) {
    super(props)
    this.state = {featuredImageSrc: undefined}
  }

  render() {
    const {event} = this.props
    return (
      // {this.state.featuredImageSrc &&
        <Image responsive alt={event.name} src={this.state.featuredImageSrc} className="centered featured-image"/>
      // }
    )
  }

  componentDidMount() {
    const {event} = this.props
    event.featuredImage.then(featuredImageSrc => this.setState({featuredImageSrc}))
  }
}

class EventPage extends Component {
  constructor(props) {
    super(props);
    const eventId = props.match.params.id;
    this.state = {event: undefined, eventId: eventId};
  }

  componentDidMount() {
    this._loadEvent(this.state.eventId);
  }

  _loadEvent(id) {
    // https://developer.wordpress.com/docs/api/
    // fetch(`https://public-api.wordpress.com/rest/v1.1/sites/pdi2017.wordpress.com/posts/${id}`)
    fetch(`http://tks.rxn.mybluehost.me/wp-json/wp/v2/events/${id}`)
      .then(res => res.json())
      .then(json => convertPostToEvent(json))
      .then(event => this.setState({event}));
  }

  render() {
    const event = this.state.event;
    if (!event) {
      return <div className="loader-wrapper"><div className="loader"><Glyphicon glyph="plane"/></div></div>;
    }
    
    return (
      <div className="event circle-bg">
      <Grid className="event-details">
        <Row>
          <Col md={12} className="space-top text-center">
            <h2 className={`highlight-text event-name ${event.featured ? 'sticky' : ''}`} dangerouslySetInnerHTML={{__html: event.name, sanitize: true}}></h2>
            {<FeaturedImage event={event} />}
          </Col>
        </Row>
        
        <Row className="margin-top margin-bottom">
          <Col className="content" md={8} mdOffset={2}>
            <div dangerouslySetInnerHTML={{__html: event.content}} />
          </Col>
          <Col md={2}>
            <h4 className="date"><Glyphicon glyph="calendar"/>{event.date}</h4>
            <p>{event.start} - {event.end}</p>
            <p><Glyphicon glyph="map-marker"/>{event.location}</p>
          </Col>
        </Row>

        <Row className="space-top">
          <Col md={10} mdOffset={1} className="text-center">
            <div className="reg-container">
              <Button href="#/contact" bsClass="reg-button text-large">Đăng kí để biết thêm chi tiết</Button>
            </div>
          </Col>
        </Row>

        <Row className="space-top">
          <Col md={10} mdOffset={1} className="text-center">
            <p className="pull-right">
              <a href="#/events" className="uppercase link-back"><Glyphicon glyph="arrow-left" />Sự kiện</a>
            </p>
          </Col>
        </Row>
      </Grid>
      </div>
    );
  }
}


EventPage.route = <Route exact path="/events/:id" component={EventPage}/>;

export default EventPage;



