import React, {Component} from 'react';
import {Route} from 'react-router-dom';
import {Grid, Row, Col, Glyphicon} from 'react-bootstrap';


const convertPostToSharing = (post) => {
  return {
    id: post.id,
    name: post.title.rendered,
    teaser: post.excerpt.rendered,
    content: post.content.rendered,
    series: post.acf.series,
    categories: post.categories,
    featured: post.acf.featured
  };
};

class SharingPage extends Component {
  constructor(props) {
    super(props);
    const sharingId = props.match.params.id;
    this.state = {sharing: undefined, sharingId: sharingId};
  }

  componentDidMount() {
    this._loadSharing(this.state.sharingId);
  }

  _loadSharing(id) {
    // https://developer.wordpress.com/docs/api/
    // fetch(`https://public-api.wordpress.com/rest/v1.1/sites/pdi2017.wordpress.com/posts/${id}`)
    fetch(`http://tks.rxn.mybluehost.me/wp-json/wp/v2/sharings/${id}`)
      .then(res => res.json())
      .then(json => convertPostToSharing(json))
      .then(sharing => this.setState({sharing}));
  }

  render() {
    const sharing = this.state.sharing;
    if (!sharing) {
      return <div className="loader-wrapper"><div className="loader"><Glyphicon glyph="plane"/></div></div>;
    }
    
    return (
      <div className="event bubble-bg">
      <Grid className="event-details">
        <Row>
          <Col md={12} className="space-top text-center">
            <h2 className={`highlight-text text-uppercase sharing-name ${sharing.featured ? 'sticky' : ''}`} dangerouslySetInnerHTML={{__html: sharing.name, sanitize: true}}></h2>
            <p className="text-italic">trong loạt bài về <strong>{sharing.series}</strong></p>
          </Col>
        </Row>
        
        <Row className="margin-top margin-bottom">
          <Col className="content" md={8} mdOffset={2}>
            <div dangerouslySetInnerHTML={{__html: sharing.content}} />
          </Col>
        </Row>

        <Row className="space-top">
          <Col md={10} mdOffset={1} className="text-center">
            <p className="pull-right">
              <a href="#/sharings" className="uppercase link-back"><Glyphicon glyph="arrow-left" />Chia sẻ</a>
            </p>
          </Col>
        </Row>
      </Grid>
      </div>
    );
  }
}


SharingPage.route = <Route exact path="/sharings/:id" component={SharingPage}/>;

export default SharingPage;



