import React, {Component} from 'react';
import {Glyphicon} from 'react-bootstrap';


const TimelineMax = window.TimelineMax;

const AdsTeaser = ({ads}) => {
  return (
    <div className="news">
      <div className="news-content">
      <p><Glyphicon glyph="calendar"/>{ads.acf.date_time} <Glyphicon glyph="map-marker" />{ads.acf.location}: {ads.acf.content}</p>
      </div>
    </div>
  );
};


class Banner extends Component {
  constructor(props) {
    super(props);
    this.state = {ads: undefined};
  }

  render() {
    if (!this.state.ads) {
      return <div className="loader-wrapper"></div>;
    }

    return (      
      <div ref={this._animate} id="sticky-banner">
        <div className="content-wrapper">
          {this.state.ads.map(c => <AdsTeaser key={c.id} ads={c} />)}
        </div>
      </div>
    );
  }

  componentDidMount() {
    this._loadAds();
  }

  _loadAds() {
    fetch('http://tks.rxn.mybluehost.me/wp-json/wp/v2/member-ads')
      .then(res => res.json())
      .then(ads => this.setState({ads}));
  }

  _animate() {
    var banner = document.getElementById("sticky-banner"),
    news = document.getElementsByClassName("news"),

    tl2 = new TimelineMax({repeat: -1});

    tl2
      .to(banner, 0, {autoAlpha: 1})
      .staggerFromTo(news, 0.5,
        {autoAlpha: 0,
          y: "-50px",
          scale: 2,
          rotation: -30,
          cycle: {
            backgroundImage:[
              "linear-gradient(120deg, #f77062 0%, #fe5196 100%)",
              "linear-gradient(-120deg, #007adf 0%, #00ecbc 100%)"
            ]}},
        {autoAlpha: 1, y: 0, scale: 1, rotation: 0},
        5) // News appears and stays for 5s
      .to(news, 0, {autoAlpha: 0}, "+=5") // Set delay to make last news stay for 5s
      .to(banner, 0, {autoAlpha: 0.5}
    )
  }
};

export default Banner;




