import React, {Component} from 'react';
import {Route} from 'react-router-dom';
import {Grid, Row, Col, Glyphicon, Image, Button} from 'react-bootstrap';


const getFeaturedImage = (id) => {
  return fetch(`http://tks.rxn.mybluehost.me/wp-json/wp/v2/media/${id}`)
    .then(res => res.json())
    .then(json => json.guid.rendered)
}

const convertPostToCourse = (post) => {
  return {
    id: post.id,
    name: post.title.rendered,
    teaser: post.excerpt.rendered,
    date: post.acf.date? post.acf.date : new Date(post.date).toLocaleDateString(),
    start: post.acf.start_time,
    end: post.acf.end_time,
    location: post.acf.location,
    featuredImage: getFeaturedImage(post.featured_media),
    content: post.content.rendered,
    featured: post.acf.featured,
    attendees: post.acf.attendees
  };
};

class FeaturedImage extends Component {
  constructor(props) {
    super(props)
    this.state = {featuredImageSrc: undefined}
  }

  render() {
    const {course} = this.props
    return (
      // {this.state.featuredImageSrc &&
        <Image responsive alt={course.name} src={this.state.featuredImageSrc} className="centered featured-image"/>
      // }
    )
  }

  componentDidMount() {
    const {course} = this.props
    course.featuredImage.then(featuredImageSrc => this.setState({featuredImageSrc}))
  }
}

class ScheduledCoursePage extends Component {
  constructor(props) {
    super(props);
    const courseId = props.match.params.id;
    this.state = {course: undefined, courseId: courseId};
  }

  componentDidMount() {
    this._loadScheduledCourse(this.state.courseId);
  }

  _loadScheduledCourse(id) {
    // https://developer.wordpress.com/docs/api/
    // fetch(`https://public-api.wordpress.com/rest/v1.1/sites/pdi2017.wordpress.com/posts/${id}`)
    fetch(`http://tks.rxn.mybluehost.me/wp-json/wp/v2/courses/${id}`)
      .then(res => res.json())
      .then(json => convertPostToCourse(json))
      .then(course => this.setState({course}));
  }

  render() {
    const course = this.state.course;
    if (!course) {
      return <div className="loader-wrapper"><div className="loader"><Glyphicon glyph="plane"/></div></div>;
    }
    
    return (
      <div className="course circle-bg">
      <Grid className="course-details">
        <Row>
          <Col md={12} className="space-top text-center">
            <h2 className={`highlight-text course-name ${course.featured ? 'sticky' : ''}`} dangerouslySetInnerHTML={{__html: course.name, sanitize: true}}></h2>
            {<FeaturedImage course={course} />}
          </Col>
        </Row>
        
        <Row className="margin-top margin-bottom">
          <Col className="content" md={8} mdOffset={2}>
            <div dangerouslySetInnerHTML={{__html: course.content}} />
            <h2 className="thin">Đối tượng tham gia</h2>
            <p>{course.attendees}</p>
          </Col>
          <Col md={2}>
            <h4 className="date"><Glyphicon glyph="calendar"/>{course.date}</h4>
            <p><Glyphicon glyph="map-marker"/>{course.location}</p>
          </Col>
        </Row>

        <Row className="space-top">
          <Col md={10} mdOffset={1} className="text-center">
            <div className="reg-container">
              <Button href="#/contact" bsClass="reg-button text-large">Đăng kí để biết thêm chi tiết</Button>
            </div>
          </Col>
        </Row>

        <Row className="space-top">
          <Col md={10} mdOffset={1} className="text-center">
            <p className="pull-right">
              <a href="#/schedules" className="uppercase link-back"><Glyphicon glyph="arrow-left" />Lịch khai giảng</a>
            </p>
          </Col>
        </Row>
      </Grid>
      </div>
    );
  }
}


ScheduledCoursePage.route = <Route exact path="/scheduled_courses/:id" component={ScheduledCoursePage}/>;

export default ScheduledCoursePage;



