import React, {Component} from 'react';
import {Route} from 'react-router-dom';

import SharingTeasers from './SharingTeasers';

class SharingsPage extends Component {
  constructor(props) {
    super(props);

    this.state = {
      lightboxOpen: false,
      currentImage: 0
    };
  }

  componentDidMount () {
    window.scrollTo(0, 0);
  }

  render() {
    return (
      <div id="events-list" className="bubble-bg">
        <h2 className="title-underline uppercase">Chia sẻ</h2>
        <SharingTeasers />
      </div>
    );
  }
}

SharingsPage.route = <Route exact path="/sharings" component={SharingsPage}/>;

export default SharingsPage;



