import React, {Component} from 'react';
import {Grid, Row, Col, Image, Glyphicon} from 'react-bootstrap';

// import events from './events.json';

const getFeaturedImage = (id) => {
  if (id === 0) {
    return Promise.resolve(undefined)
  }

  return fetch(`http://tks.rxn.mybluehost.me/wp-json/wp/v2/media/${id}`)
    .then(res => res.json())
    .then(json => json.guid.rendered)
}

const convertPostToEvent = (post) => {
  return {
    id: post.id,
    name: post.title.rendered,
    teaser: post.excerpt.rendered,
    date: post.acf.date,
    start: post.acf.start_time,
    end: post.acf.end_time,
    location: post.acf.location,
    featuredImage: getFeaturedImage(post.featured_media),
    featured: post.acf.featured
  };
};

class EventTeaser extends Component {
  constructor(props) {
    super(props)
    this.state = {featuredImageSrc: undefined}
  }

  render() {
    const {event} = this.props
    return (
      <Row className="clearfix event-item">
        <Col sm={4} md={4}>
          {this.state.featuredImageSrc &&
            <a href={`/#/events/${event.id}`}><Image responsive alt={event.name} src={this.state.featuredImageSrc} /></a>
          }
        </Col>
        <Col md={6}>
          <h3>
            <a href={`/#/events/${event.id}`} dangerouslySetInnerHTML={{__html: event.name, sanitize: true}}></a>
            {event.featured &&
              <div className="featured"></div>
            }
          </h3>
          <p><Glyphicon glyph="map-marker"/>{event.location}</p>
          <div dangerouslySetInnerHTML={{__html: event.teaser}} />
        </Col>
        <Col md={2}>
          <h4 className="date"><Glyphicon glyph="calendar"/>{event.date}</h4>
          <p>{event.start}-{event.end}</p>
        </Col>
      </Row>
    )
  }

  componentDidMount() {
    const {event} = this.props
    event.featuredImage.then(featuredImageSrc => this.setState({featuredImageSrc}))
  }
}

class EventTeasers extends Component {
  constructor(props) {
    super(props);
    this.state = {events: undefined};
  }

  render() {
    if (!this.state.events) {
      return <div className="loader-wrapper"><div className="loader"><Glyphicon glyph="plane"/></div></div>;
    }

    return (      
      <div>
        <Grid className="events space-bottom">
          {this.state.events.map(c => <EventTeaser key={c.id} event={c} />)}
        </Grid>
      </div>
    );
  }

  componentDidMount() {
    this._loadEvents();
  }

  _loadEvents() {
    // https://developer.wordpress.com/docs/api/
    // fetch('https://public-api.wordpress.com/rest/v1.1/sites/pdi2017.wordpress.com/posts/?category=events')
    fetch('http://tks.rxn.mybluehost.me/wp-json/wp/v2/events')
      .then(res => res.json())
      .then(json => json.map(convertPostToEvent))
      .then(events => this.setState({events}));
  }
};

export default EventTeasers;



