import React, {Component} from 'react';
import {Grid, Row, Col, Glyphicon} from 'react-bootstrap';


const convertPostToSharing = (post) => {
  return {
    id: post.id,
    name: post.title.rendered,
    teaser: post.excerpt.rendered,
    series: post.acf.series,
    featured: post.acf.featured
  };
};

const SharingTeaser = ({sharing}) => {
  return (
    <Row className="clearfix schedule-item">
      <Col md={3}>
        <h4 className="text-italic text-right">{sharing.series}</h4>
      </Col>
      <Col md={9}>
        <h3 className="text-uppercase">
          <a href={`/#/sharings/${sharing.id}`} dangerouslySetInnerHTML={{__html: sharing.name, sanitize: true}}></a>
          {sharing.featured &&
            <div className="featured"></div>
          }
        </h3>
        <div dangerouslySetInnerHTML={{__html: sharing.teaser}} />
      </Col>
    </Row>
  )
}

class SharingTeasers extends Component {
  constructor(props) {
    super(props);
    this.state = {sharings: undefined};
  }

  render() {
    if (!this.state.sharings) {
      return <div className="loader-wrapper"><div className="loader"><Glyphicon glyph="plane"/></div></div>;
    }

    return (      
      <div>
        <Grid className="sharings space-bottom">
          {this.state.sharings.map(c => <SharingTeaser key={c.id} sharing={c} />)}
        </Grid>
      </div>
    );
  }

  componentDidMount() {
    this._loadSharings();
  }

  _loadSharings() {
    fetch('http://tks.rxn.mybluehost.me/wp-json/wp/v2/sharings')
      .then(res => res.json())
      .then(json => json.map(convertPostToSharing))
      .then(sharings => this.setState({sharings}));
  }
};

export default SharingTeasers;



